body {
  margin: 0;
  height: 100vh;
  background: radial-gradient(circle, rgba(8, 1, 54, 1) 15%,
      rgba(6, 1, 41, 1) 43%, rgba(4, 1, 29, 1) 99%, rgba(0, 0, 0, 1) 100%);
}

@import url('https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,500;0,600;0,900;1,100;1,300;1,900&family=Playfair+Display:ital,wght@1,700&family=Poppins:wght@300;400&family=Roboto:ital,wght@0,100;0,500;1,300&family=Rowdies:wght@300;700&display=swap');

/* font-family: 'Kanit', sans-serif;
font-family: 'Playfair Display', serif;
font-family: 'Poppins', sans-serif;
font-family: 'Roboto', sans-serif;
font-family: 'Rowdies', cursive; */
* {
  margin: 0;
  font-family: 'Kanit', sans-serif;
  font-weight: 300;
}

.App {
  height: 100vh;
  align-items: center;
  justify-content: center;
  display: flex;
}

.stopwatch {
  padding: 2rem;
  background-color: white;
  border-radius: 1rem;
  display: grid;
  text-align: center;
  box-shadow: 0px 0px 28px 0px rgba(0, 0, 0, 0.75);
  font-size: 5rem;
}

p {
  padding: 1rem;
}

button {
  padding: .5rem 1.5rem;
  border-style: none;
  background-color: black;
  color: white;
  border-radius: 0.5rem;
  margin: 0 0.5rem;
  width: 10rem;
  height: 3rem;
  font-size: 1.1rem;
  cursor: pointer;
}

button:hover {
  background-color: rgb(14, 13, 13);
}

@media (max-width:442px) {
  .stopwatch {
    padding: 0;
  }
}

@media (max-width:380px) {
  .stopwatch {
    font-size: 3rem;
    width: 95%;
  }
  .actions{
    padding: 0 0 2rem 0;
  }
}